l-table-column>\n\n        <el-table-column label=\"推荐码\"\n                         align=\"center\"\n                         prop=\"share_promo_code\">\n\n        </el-table-column>\n        <el-table-column prop=\"create_time\"\n                         label=\"时间\"\n                         align=\"center\"></el-table-column>\n        <el-table-column label=\"操作\"\n                         align=\"center\">\n          <template slot-scope=\"scope\">\n            <el-button type=\"primary\"\n                       size=\"small\"\n                       @click=\"issue(scope.row)\">发放</el-button>\n          </template>\n        </el-table-column>\n      </el-table>\n      <!-- 分页 -->\n      <div :class=\"list && list.length<=7?'page-nav':'page_center'\"\n           v-show=\"list && list.length>0\">\n        <paging :page-count=\"pageCount\"\n                :page=\"page\"\n                @index=\"paging\"></paging>\n        <!--分页的组件-->\n      </div>\n    </div>\n\n  </div>\n</template>\n<script type=\"text/ecmascript-6\">\n\nexport default {\n  data () {\n    return {\n      list: [],\n      token: window.sessionStorage.getItem('bbytoken'),\n      page: 0,\n      pageCount: 0,\n      authList: [],\n      threeAuthList: []\n    }\n  },\n  created () {\n    this.init();\n  },\n\n  methods: {\n\n    paging (e) {\n      this.page = e;\n      this.init()\n    },\n    init () {\n      this.$axios.post('admin/ShopAudit/rewardShop', { token: this.token, page: this.page })\n        .then(res => {\n\n          this.list = res.data.data.data;\n\n          this.pageCount = res.data.data.rows;\n        }).catch(err => { })\n    },\n\n    issue (row) {  //发放\n      this.$confirm('此操作将发放, 是否继续?', '提示', {\n        confirmButtonText: '确定',\n        cancelButtonText: '取消',\n        type: 'warning',\n        center: true\n      }).then(async () => {\n        try {\n          const res = await this.$axios.post('admin/ShopAudit/rewardUidHandle', { token: this.token, sid: row.id, uid: row.uid })\n          if (res.data.code == 1) {\n            this.$message({ message: res.data.msg, type: \"success\" })\n            this.init()\n          } else {\n            this.$message.error(res.data.msg)\n          }\n\n        } catch (err) {\n          throw (err)\n        }\n      }).catch(() => { });\n\n    }\n\n  },\n  mounted () {\n    var id = this.$route.query.id;\n    this.curId = id;\n    this.$axios.post('admin/Auth/erAuth', {\n      token: window.sessionStorage.getItem('bbytoken'),\n      id: id\n    })\n      .then(res => {\n\n        if (res.data.code == 1) {\n          var arr = res.data.data;\n          for (var i = 0; i < arr.length; i++) {\n            if (arr[i].son) {\n              if (arr[i].name == '开发奖励发放') {\n                this.seCurId = arr[i].id;\n                this.threeAuthList = arr[i].son;\n              }\n              for (var j = 0; j < arr[i].son.length; j++) {\n                if (arr[i].action != arr[i].son[j].action) {\n                  arr[i].action = arr[i].son[0].action;\n                }\n                if (arr[i].son[j].name == '待发放' && arr[i].name == '开发奖励发放') {\n                  this.thCurId = arr[i].son[j].id;\n                }\n              }\n            }\n          }\n          this.authList = arr;\n        } else {\n          this.$alert(res.data.msg, '提示', { type: 'error' });\n        }\n      })\n      .catch(err => {\n        alert(err);\n      })\n  },\n}\n</script>\n<style scoped>\n.ellipsis {\n  width: 150px;\n  overflow: hidden;\n  text-overflow: ellipsis;\n  display: -webkit-box;\n  -webkit-box-orient: vertical;\n  -webkit-line-clamp: 2;\n}\n.laybox2 ul {\n  margin: 33px auto;\n  border-bottom: 1px dashed #04bbfc;\n}\n\n.laybox2 {\n  padding: 60px;\n  font-size: 16px;\n}\n\n.laybox2 li {\n  width: 300px;\n  text-align: left;\n  margin: 33px auto;\n}\n\n.t-c:after {\n  content: \"\";\n  display: block;\n  height: 0;\n  clear: both;\n}\n\n.bot > div {\n  position: relative;\n}\n\ndiv.bot {